import React from "react";
import Layout from "./../../components/layout/layout";
import MenuAdmin from "../../components/layout/MenuAdmin";
import { useAuth } from "../../context/auth";

const AdminPanel = () => {
  const [auth] = useAuth();
  return (
    <Layout>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <div className="card w-75 p-3">
              <h3 style={{ fontFamily: "Formula1" }}>
                Nombre del Administrador : {auth?.user?.nombre}
              </h3>
              <h3 style={{ fontFamily: "Formula1" }}>
                Email del Administrador : {auth?.user?.email}
              </h3>
              <h3 style={{ fontFamily: "Formula1" }}>
                Contacto del Administrador : {auth?.user?.telefono}
              </h3>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default AdminPanel;
