import React, { useState, useEffect } from "react";
import Layout from "./../../components/layout/layout";
import MenuAdmin from "./../../components/layout/MenuAdmin";
import toast from "react-hot-toast";
import axios from "axios";
import { Select } from "antd";
import { useNavigate, useParams } from "react-router-dom";
const { Option } = Select;

const UpdateProduct = () => {
  const navigate = useNavigate();
  const params = useParams();
  const [categorias, setCategorias] = useState([]);
  const [nombre, setNombre] = useState("");
  const [descripcion, setDescripcion] = useState("");
  const [precio, setPrecio] = useState("");
  const [categoria, setCategoria] = useState("");
  const [cantidad, setCantidad] = useState("");
  const [envio, setEnvio] = useState("");
  const [foto, setFoto] = useState("");
  const [id, setId] = useState("");

  //get single product
  const getSingleProduct = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/producto/get-producto/${params.slug}`
      );
      setNombre(data.producto.nombre);
      setId(data.producto._id);
      setDescripcion(data.producto.descripcion);
      setPrecio(data.producto.precio);
      setPrecio(data.producto.precio);
      setCantidad(data.producto.cantidad);
      setEnvio(data.producto.envio);
      setCategoria(data.producto.category._id);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getSingleProduct();
    //eslint-disable-next-line
  }, []);
  //get all category
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get("/api/v1/categoria/get-categoria");
      if (data?.success) {
        setCategorias(data?.categoria);
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal al obtener la categoría");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  //create product function
  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("nombre", nombre);
      productData.append("descripcion", descripcion);
      productData.append("precio", precio);
      productData.append("cantidad", cantidad);
      foto && productData.append("foto", foto);
      productData.append("categoria", categoria);
      const { data } = axios.put(
        `/api/v1/producto/actualizar-producto/${id}`,
        productData
      );
      if (data?.success) {
        toast.error(data?.message);
      } else {
        toast.success("Producto Actualizado Correctamente");
        navigate("/panel/admin/productos");
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal");
    }
  };

  //delete a product
  const handleDelete = async () => {
    try {
      let answer = window.prompt(
        "Estás seguro que quieres borrar este producto? "
      );
      if (!answer) return;
      const { data } = await axios.delete(
        `/api/v1/producto/delete-producto/${id}`
      );
      toast.success("Producto Borrado Correctamente");
      navigate("/panel/admin/productos");
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal");
    }
  };
  return (
    <Layout title={"Panel - Actualizar Producto"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <h1 style={{ color: "white" }}>Actualizar producto</h1>
            <div className="m-1 w-75">
              <Select
                bordered={false}
                placeholder="Selecciona una categoría"
                size="large"
                showSearch
                className="form-select mb-3"
                onChange={(value) => {
                  setCategoria(value);
                }}
                value={categoria.nombre}
              >
                {categorias?.map((c) => (
                  <Option key={c._id} value={c._id}>
                    {c.nombre}
                  </Option>
                ))}
              </Select>
              <div className="mb-3">
                <label className="btn btn-outline-danger col-md-12">
                  {foto ? foto.name : "Subir Foto"}
                  <input
                    type="file"
                    name="foto"
                    accept="image/*"
                    onChange={(e) => setFoto(e.target.files[0])}
                    hidden
                  />
                </label>
              </div>
              <div className="mb-3">
                {foto ? (
                  <div className="text-center">
                    <img
                      src={URL.createObjectURL(foto)}
                      alt="product_photo"
                      height={"200px"}
                      className="img img-responsive"
                    />
                  </div>
                ) : (
                  <div className="text-center">
                    <img
                      src={`/api/v1/producto/producto-foto/${id}`}
                      alt="product_photo"
                      height={"200px"}
                      className="img img-responsive"
                    />
                  </div>
                )}
              </div>
              <div className="mb-3">
                <input
                  type="text"
                  value={nombre}
                  placeholder="Escribe un nombre"
                  className="form-control"
                  onChange={(e) => setNombre(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <textarea
                  type="text"
                  value={descripcion}
                  placeholder="Escribe una descripción"
                  className="form-control"
                  onChange={(e) => setDescripcion(e.target.value)}
                />
              </div>

              <div className="mb-3">
                <input
                  type="number"
                  value={precio}
                  placeholder="Precio"
                  className="form-control"
                  onChange={(e) => setPrecio(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <input
                  type="number"
                  value={cantidad}
                  placeholder="Cantidad"
                  className="form-control"
                  onChange={(e) => setCantidad(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <Select
                  bordered={false}
                  placeholder="Selecciona Envío"
                  size="large"
                  showSearch
                  className="form-select mb-3"
                  onChange={(value) => {
                    setEnvio(value);
                  }}
                  value={envio ? "Si" : "No"}
                >
                  <Option value="0">No</Option>
                  <Option value="1">Si</Option>
                </Select>
              </div>
              <div className="mb-3">
                <button className="btn btn-danger" onClick={handleUpdate}>
                  ACTUALIZAR PRODUCTO
                </button>
              </div>
              <div className="mb-3">
                <button
                  className="btn btn-danger"
                  style={{ backgroundColor: "black", color: "red" }}
                  onClick={handleDelete}
                >
                  ELIMINAR PRODUCTO
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default UpdateProduct;
