import React, { useState, useEffect } from "react";
import Layout from "../components/layout/layout";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import { Radio } from "antd";
import { Precios } from "../components/Precios";
import { useCarro } from "../context/carro";
import toast from "react-hot-toast";
import "../styles/fondo.css";

const Home = () => {
  const navigate = useNavigate();
  const [carro, setCarro] = useCarro();
  const [productos, setProductos] = useState([]);
  const [categorias, setCategorias] = useState([]);
  const [checked, setChecked] = useState([]);
  const [radio, setRadio] = useState([]);

  // Obtener todas las categorias
  const getTodasCategorias = async () => {
    try {
      const { data } = await axios.get("/api/v1/categoria/get-categoria");
      if (data?.success) {
        setCategorias(data?.categoria);
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getTodasCategorias();
  }, []);

  // Obtener productos
  const getTodosProductos = async () => {
    try {
      const { data } = await axios.get("/api/v1/producto/get-producto");
      setProductos(data.productos);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (!checked.length || !radio.length) getTodosProductos();
  }, [checked.length, radio.length]);

  useEffect(() => {
    if (checked.length || radio.length) filtrarProducto();
  }, [checked, radio]);

  // Obtener el filtrado de productos
  const filtrarProducto = async () => {
    if (checked.length || radio.length) {
      try {
        const { data } = await axios.post("/api/v1/producto/producto-filtros", {
          checked,
          radio,
        });
        setProductos(data?.productos);
      } catch (error) {
        console.log(error);
      }
    } else {
      getTodosProductos();
    }
  };

  return (
    <Layout title={"Footie Fuel - Todos los productos"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "20px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="col-md-3 filters text-center">
          {/* Filtrado de precios */}
          <h4 className="text-center mt-4" style={{ color: "white" }}>
            Filtrar por Precio
          </h4>
          <div className="d-flex flex-column">
            <Radio.Group
              style={{ color: "white" }}
              onChange={(e) => setRadio(e.target.value)}
            >
              {Precios?.map((p) => (
                <div key={p._id}>
                  <Radio
                    style={{ color: "white", fontFamily: "Formula1" }}
                    value={p.array}
                  >
                    {p.nombre}
                  </Radio>
                </div>
              ))}
            </Radio.Group>
          </div>
          <div className="d-flex flex-column">
            <button
              style={{ marginTop: "20px", marginBottom: "20px" }}
              className="btn btn-danger"
              onClick={() => window.location.reload()}
            >
              REESTABLECER FILTROS
            </button>
          </div>
        </div>
        <div className="col-md-9">
          <h1 className="text-center" style={{ color: "white" }}>
            Todos los productos
          </h1>
          <div className="d-flex flex-wrap text-center">
            {productos?.map((p) => (
              <div
                className="card m-3 inicio"
                style={{ width: "18rem" }}
                key={p._id}
              >
                <img
                  src={`/api/v1/producto/producto-foto/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <h5 className="card-title">{p.nombre}</h5>
                  <p className="card-text">
                    {p.descripcion.substring(0, 30)}...
                  </p>
                  <p className="card-text">{p.precio} €</p>
                  <button
                    style={{ color: "black" }}
                    className="btn btn-danger ms-2"
                    onClick={() => navigate(`/producto/${p.slug}`)}
                  >
                    Detalles
                  </button>
                  <button
                    style={{ color: "white", backgroundColor: "black" }}
                    className="btn btn-secondary ms-2"
                    onClick={() => {
                      setCarro([...carro, p]);
                      localStorage.setItem(
                        "carro",
                        JSON.stringify([...carro, p])
                      );
                      toast.success("Artículo añadido al carrito");
                    }}
                  >
                    Añadir al carrito
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Home;
