import React from "react";
import Layout from "./../components/layout/layout";
const Politicas = () => {
  return (
    <Layout title={"Políticas de privacidad"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingTop: "20px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <h1
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Políticas de Privacidad
        </h1>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          En Footie Fuel, nos tomamos muy en serio tu privacidad y nos
          comprometemos a protegerla.
        </p>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Esta Política de Privacidad describe cómo recopilamos, utilizamos y
          protegemos la información personal que nos proporcionas cuando
          utilizas nuestros servicios.
        </p>

        <h3
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Recopilación de información
        </h3>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Recopilamos información personal que nos proporcionas cuando te
          registras en nuestra tienda en línea, realizas una compra, nos envías
          un mensaje a través de nuestro formulario de contacto o te suscribes a
          nuestro boletín informativo.
        </p>

        <h3
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Uso de la información
        </h3>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Utilizamos la información recopilada para procesar tus pedidos,
          brindar un servicio al cliente eficiente, personalizar tu experiencia
          de compra y enviarte actualizaciones y ofertas relevantes.
        </p>

        <h3
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Divulgación de información
        </h3>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          No vendemos, intercambiamos ni transferimos tu información personal a
          terceros sin tu consentimiento, excepto en los casos necesarios para
          procesar tus pedidos o cumplir con la ley.
        </p>

        <h3
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Seguridad de la información
        </h3>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Tomamos medidas razonables para proteger tu información personal
          contra accesos no autorizados o divulgación.
        </p>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Sin embargo, no podemos garantizar la seguridad absoluta de la
          información transmitida por Internet.
        </p>

        <h3
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Cambios a esta Política de Privacidad
        </h3>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Nos reservamos el derecho de actualizar esta Política de Privacidad en
          cualquier momento.
        </p>
        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Te recomendamos revisar periódicamente esta página para estar al tanto
          de los cambios.
        </p>

        <p
          style={{
            fontFamily: "Formula1",
            color: "white",
          }}
        >
          Si tienes alguna pregunta o inquietud sobre nuestra Política de
          Privacidad, no dudes en contactarnos.
        </p>
      </div>
    </Layout>
  );
};

export default Politicas;
