import React from "react";
import Layout from "./../components/layout/layout";
const Contacto = () => {
  return (
    <Layout title={"Contacta con Nosotros"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingTop: "20px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="col-md-6">
          <h3
            style={{
              fontFamily: "Formula1",
              color: "white",
            }}
          >
            Contacto
          </h3>
          <p
            style={{
              fontFamily: "Formula1",
              color: "white",
            }}
          >
            ¿Tienes alguna pregunta o comentario? ¡Nos encantaría saber de ti!
          </p>
          <p
            style={{
              fontFamily: "Formula1",
              color: "white",
            }}
          >
            Puedes ponerte en contacto con nosotros a través de los siguientes
            medios:
          </p>
        </div>
        <div className="col-md-6">
          <h3
            style={{
              fontFamily: "Formula1",
              color: "white",
            }}
          >
            Información de contacto:
          </h3>
          <ul style={{ fontFamily: "Formula1", color: "white" }}>
            <li>Teléfono: +123456789</li>
            <li>Correo electrónico: info@footiefuel.com</li>
            <li>Dirección: Calle Ejemplo, Ciudad, País</li>
          </ul>
        </div>
      </div>
    </Layout>
  );
};

export default Contacto;
