import React from "react";
import Layout from "./../components/layout/layout";
import { useBuscar } from "../context/buscar";

const Buscar = () => {
  const [valores, setValores] = useBuscar();
  return (
    <Layout title={"Resultados de la búsqueda"}>
      <div className="container">
        <div className="text-center">
          <h1 className="bus">Resultados de la Búsqueda</h1>
          <h6>
            {valores?.resultado.length < 1
              ? "No se han encotrado productos"
              : `Encontrado ${valores?.resultado.length}`}
          </h6>
          <div className="d-flex flex-wrap mt-4">
            {valores?.resultado.map((p) => (
              <div className="card m-1" style={{ width: "20rem" }}>
                <img
                  src={`/api/v1/producto/producto-foto/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <h5 className="card-title">{p.nombre}</h5>
                  <p className="card-text">
                    {p.descripcion.substring(0, 30)}...
                  </p>
                  <p className="card-text">{p.precio} €</p>
                  <button class="btn btn-primary ms-2">Detalles</button>
                  <button class="btn btn-secondary ms-2">
                    Añadir al carrito
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Buscar;
