import React, { useEffect, useState } from "react";
import Layout from "./../../components/layout/layout";
import MenuAdmin from "./../../components/layout/MenuAdmin";
import toast from "react-hot-toast";
import axios from "axios";
import CategoriaFormulario from "../../components/Form/CategoriaFormulario";
import { Modal } from "antd";
const CrearCategoria = () => {
  const [categorias, setCategorias] = useState([]);
  const [nombre, setNombre] = useState("");
  const [visible, setVisible] = useState(false);
  const [seleccionado, setSeleccionado] = useState(null);
  const [nombreActualizado, setNombreActualizado] = useState("");
  //Formulario
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.post("/api/v1/categoria/crear-categoria", {
        nombre,
      });
      if (data?.success) {
        toast.success(`La categoria ${nombre} ha sido creada`);
        getTodasCategorias();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal en el formulario");
    }
  };
  //obtener todas las categorias
  const getTodasCategorias = async () => {
    try {
      const { data } = await axios.get("/api/v1/categoria/get-categoria");
      if (data?.success) {
        setCategorias(data?.categoria);
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal al obtener la categoría");
    }
  };

  useEffect(() => {
    getTodasCategorias();
  }, []);

  //actualizar categoria
  const handleActualizado = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put(
        `/api/v1/categoria/actualizar-categoria/${seleccionado._id}`,
        { nombre: nombreActualizado }
      );
      if (data?.success) {
        toast.success(`${nombreActualizado} ha sido actualizado`);
        setSeleccionado(null);
        setNombreActualizado("");
        setVisible(false);
        getTodasCategorias();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      console.log(error);
    }
  };

  //borrar categoria
  const handleBorrar = async (pId) => {
    try {
      const { data } = await axios.delete(
        `/api/v1/categoria/borrar-categoria/${pId}`
      );
      if (data.success) {
        toast.success(`La categoria ha sido borrada`);

        getTodasCategorias();
      } else {
        toast.error(data.message);
      }
    } catch (error) {
      toast.error("Algo ha ido mal");
    }
  };

  return (
    <Layout title={"Panel - Crear Categoria"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <h1 style={{ color: "white" }}>Control de Categorias</h1>
            <div className="p-3 w-50">
              <CategoriaFormulario
                handleSubmit={handleSubmit}
                value={nombre}
                setValue={setNombre}
              />
            </div>
            <div className="w-75">
              <table className="table">
                <thead>
                  <tr>
                    <th
                      scope="col"
                      style={{ fontFamily: "Formula1", color: "white" }}
                    >
                      Nombre
                    </th>
                    <th
                      scope="col"
                      style={{ fontFamily: "Formula1", color: "white" }}
                    >
                      Acciones
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {categorias?.map((c) => (
                    <>
                      <tr>
                        <td
                          style={{ fontFamily: "Formula1", color: "white" }}
                          key={c._id}
                        >
                          {c.nombre}
                        </td>
                        <td>
                          <button
                            className="btn btn-danger ms-2"
                            onClick={() => {
                              setVisible(true);
                              setNombreActualizado(c.nombre);
                              setSeleccionado(c);
                            }}
                          >
                            Editar
                          </button>
                          <button
                            style={{ backgroundColor: "black", color: "red" }}
                            className="btn btn-danger ms-2"
                            onClick={() => {
                              handleBorrar(c._id);
                            }}
                          >
                            Borrar
                          </button>
                        </td>
                      </tr>
                    </>
                  ))}
                </tbody>
              </table>
            </div>
            <Modal
              onCancel={() => setVisible(false)}
              footer={null}
              visible={visible}
            >
              <CategoriaFormulario
                value={nombreActualizado}
                setValue={setNombreActualizado}
                handleSubmit={handleActualizado}
              />
            </Modal>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CrearCategoria;
