import React, { useState, useEffect } from "react";
import Layout from "../components/layout/layout";
import axios from "axios";
import { useParams, useNavigate } from "react-router-dom";

const FutbolProductos = () => {
  const params = useParams();
  const navigate = useNavigate();
  const [productos, setProducto] = useState([]);
  const [categoriafut, setCategoriafut] = useState([]);

  useEffect(() => {
    const getProductosporCategoria = async () => {
      try {
        const { data } = await axios.get(
          `/api/v1/producto/producto-fut/${params.slug}`
        );
        setProducto(data?.productos);
        setCategoriafut(data?.categoriafut);
      } catch (error) {
        console.log(error);
      }
    };

    if (params?.slug) {
      getProductosporCategoria();
    }
  }, [params.slug]);

  return (
    <Layout>
      <div
        className="container-fluid row mt-6 home-page background-image4 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "250px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <h4 className="text-center" style={{ color: "black" }}>
          Categoría - {categoriafut?.nombre}
        </h4>
        <h6 className="text-center" style={{ color: "red" }}>
          {productos?.length} resultados encontrados
        </h6>
        <div className="row text-center">
          <div className="d-flex flex-wrap">
            {productos?.map((p) => (
              <div
                className="card m-3 fut"
                style={{ width: "20rem" }}
                key={p._id}
              >
                <img
                  src={`/api/v1/producto/producto-foto/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <h5 className="card-title">{p.nombre}</h5>
                  <p className="card-text">
                    {p.descripcion.substring(0, 30)}...
                  </p>
                  <p className="card-text">{p.precio} €</p>
                  <button
                    className="btn btn-danger ms-2"
                    onClick={() => navigate(`/producto/${p.slug}`)}
                  >
                    Detalles
                  </button>
                  <button
                    className="btn btn-danger ms-2"
                    style={{ backgroundColor: "black", color: "red" }}
                  >
                    Añadir al carrito
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default FutbolProductos;
