import React, { useState, useEffect } from "react";
import Layout from "./../components/layout/layout";
import { useCarro } from "../context/carro";
import axios from "axios";
import { useParams } from "react-router-dom";
import toast from "react-hot-toast";
const ProductoDetalles = () => {
  const params = useParams();
  const [carro, setCarro] = useCarro();
  const [producto, setProducto] = useState({});
  const [relacionadoProducto, setRelacionadoProducto] = useState([]);

  //detalles del producto
  useEffect(() => {
    if (params?.slug) getProducto();
    //eslint-disable-next-line
  }, [params?.slug]);

  //obtener el producto
  const getProducto = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/producto/get-producto/${params.slug}`
      );
      setProducto(data?.producto);
      getSimilaresProductos(data?.producto._id, data?.producto.categoria._id);
    } catch (error) {
      console.log(error);
    }
  };

  //obtener productos similares
  const getSimilaresProductos = async (pid, cid) => {
    try {
      const { data } = await axios.get(
        `/api/v1/producto/producto-relacionado/${pid}/${cid}`
      );
      setRelacionadoProducto(data?.productos);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Layout>
      <div
        className="container-fluid background-image2 custom-container"
        style={{
          paddingBottom: "20px",
          paddingTop: "50px",
          minHeight: "100vh", // Asegura que el contenedor ocupe al menos el 100% del alto de la ventana
          display: "flex",
          flexDirection: "column",
        }}
      >
        <div className="row container mt-2">
          <div className="col-md-5">
            <img
              src={`/api/v1/producto/producto-foto/${producto._id}`}
              className="card-img-top"
              alt={producto.name}
              height="300"
              width={"350px"}
            />
          </div>
          <div className="col-md-7">
            <h1 className="text" style={{ color: "white" }}>
              Detalles del Producto
            </h1>
            <h6 style={{ color: "white" }}>Nombre: {producto.nombre}</h6>
            <h6 style={{ color: "white" }}>
              Descripción: {producto.descripcion}
            </h6>
            <h6 style={{ color: "white" }}>Precio: {producto.precio}</h6>
          </div>
        </div>
        <hr />
        <div className="row container flex-grow-1">
          <h6 style={{ color: "white" }}>Productos Relacionados</h6>
          {relacionadoProducto.length < 1 && (
            <p
              className="text-center"
              style={{ color: "white", fontFamily: "Formula1" }}
            >
              No se han encontrado productos relacionados
            </p>
          )}
          <div className="d-flex flex-wrap">
            {relacionadoProducto?.map((p) => (
              <div className="card m-1 text-center" style={{ width: "20rem" }}>
                <img
                  src={`/api/v1/producto/producto-foto/${p._id}`}
                  className="card-img-top"
                  alt={p.name}
                />
                <div className="card-body">
                  <h5 className="card-title">{p.nombre}</h5>
                  <p className="card-text">
                    {p.descripcion.substring(0, 30)}...
                  </p>
                  <p className="card-text">{p.precio} €</p>
                  <button
                    className="btn btn-danger ms-2"
                    onClick={() => {
                      setCarro([...carro, p]);
                      localStorage.setItem(
                        "carro",
                        JSON.stringify([...carro, p])
                      );
                      toast.success("Artículo añadido al carrito");
                    }}
                  >
                    Añadir al carrito
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProductoDetalles;
