import React from "react";
import { useBuscar } from "../../context/buscar";
import axios from "axios";
import { useNavigate } from "react-router-dom";
const BuscarEntrada = () => {
  const [valores, setValores] = useBuscar();
  const navigate = useNavigate();
  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.get(
        `/api/v1/producto/buscar/${valores.keyword}`
      );
      setValores({ ...valores, resultado: data });
      navigate("/buscar");
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div>
      <form className="d-flex" role="search" onSubmit={handleSubmit}>
        <input
          className="form-control me-2"
          type="search"
          id="username"
          name="username"
          autoComplete="Real Madrid"
          placeholder="Búsqueda"
          aria-label="Search"
          style={{ marginTop: "20px", fontFamily: "Formula1" }}
          value={valores.keyword}
          onChange={(e) => setValores({ ...valores, keyword: e.target.value })}
        />
        <button
          className="btn btn-danger"
          style={{ marginRight: "100px", marginTop: "25px" }}
          type="submit"
        >
          Buscar
        </button>
      </form>
    </div>
  );
};

export default BuscarEntrada;
