import React, { useState, useEffect } from "react";
import Layout from "./../../components/layout/layout";
import MenuUsuario from "../../components/layout/MenuUsuario";
import { useAuth } from "../../context/auth";
import toast from "react-hot-toast";
import axios from "axios";

const Perfil = () => {
  const [auth, setAuth] = useAuth();

  const [nombre, setNombre] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [telefono, setTelefono] = useState("");
  const [direccion, setDireccion] = useState("");

  //obtener datos de usuario
  useEffect(() => {
    const { email, nombre, telefono, direccion } = auth?.user;
    setNombre(nombre);
    setTelefono(telefono);
    setEmail(email);
    setDireccion(direccion);
  }, [auth?.user]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { data } = await axios.put("/api/v1/auth/perfil", {
        nombre,
        email,
        password,
        telefono,
        direccion,
      });
      if (data?.error) {
        toast.error(data?.error);
      } else {
        setAuth({ ...auth, user: data?.actualizarUsuario });
        let ls = localStorage.getItem("auth");
        ls = JSON.parse(ls);
        ls.user = data.actualizarUsuario;
        localStorage.setItem("auth", JSON.stringify(ls));
        toast.success("Perfil Actualizado Correctamente");
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha salido mal");
    }
  };

  return (
    <Layout title={"Tu Perfil - Footie Fuel"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3" style={{ fontFamily: "Formula1" }}>
            <MenuUsuario />
          </div>
          <div className="col-md-9" style={{ fontFamily: "Formula1" }}>
            <div className="form-container">
              <form onSubmit={handleSubmit}>
                <h4 className="titulo">Perfil de Usuario</h4>
                <div className="mb-3">
                  <input
                    type="text"
                    value={nombre}
                    onChange={(e) => setNombre(e.target.value)}
                    className="form-control"
                    id="exampleInputEmail1"
                    placeholder="Nombre"
                    autoFocus
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="form-control"
                    id="exampleInputEmail2"
                    placeholder="E-mail"
                    disabled
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="password"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    className="form-control"
                    id="exampleInputPassword3"
                    placeholder="Contraseña"
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={telefono}
                    onChange={(e) => setTelefono(e.target.value)}
                    className="form-control"
                    id="exampleInputEmail4"
                    placeholder="Teléfono"
                  />
                </div>
                <div className="mb-3">
                  <input
                    type="text"
                    value={direccion}
                    onChange={(e) => setDireccion(e.target.value)}
                    className="form-control"
                    id="exampleInputEmail5"
                    placeholder="Dirección"
                  />
                </div>
                <button type="submit" className="btn btn-primary">
                  <span>Actualizar</span>
                  <div className="top" />
                  <div className="left" />
                  <div className="bottom" />
                  <div className="right" />
                </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Perfil;
