import React from "react";
import Layout from "./../../components/layout/layout";
import MenuUsuario from "../../components/layout/MenuUsuario";
import { useAuth } from "../../context/auth.js";
const panel = () => {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [auth] = useAuth();
  return (
    <Layout title={"Panel de usuario - Footie Fuel"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3" style={{ fontFamily: "Formula1" }}>
            <MenuUsuario />
          </div>
          <div className="col-md-9">
            <div className="card w-75 p-3" style={{ fontFamily: "Formula1" }}>
              <h3>{auth?.user?.nombre}</h3>
              <h3>{auth?.user?.email}</h3>
              <h3>{auth?.user?.direccion}</h3>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default panel;
