import React from "react";
import { Link } from "react-router-dom";
import Layout from "../components/layout/layout";
import usarCategoria from "../hooks/usarCategoria";
import "../styles/fondo.css";

const Categorias = () => {
  const categorias = usarCategoria();

  return (
    <Layout title={"Todas las categorias"}>
      <div
        className="container-fluid row mt-6 home-page background-image3 custom-container"
        style={{
          paddingBottom: "100px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          {categorias.map((c) => (
            <div className="col-md-6 mt-5 mb-3" key={c._id}>
              <div className="row align-items-center">
                <div className="col-md-12 text-center">
                  <Link to={`/f1/${c.slug}`} className="btn btn-danger">
                    {c.nombre}
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      <div className="fondo-container">
        <div className="aston">
          <img
            className="aston-foto"
            src="/images/aston.png"
            alt="Aston"
            width="170px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container2">
        <div className="alfa">
          <img
            className="alfa-foto"
            src="/images/Alfa.png"
            alt="Alfa"
            width="170px"
            height="50px"
          />
        </div>
      </div>

      <div className="fondo-container3">
        <div className="merc">
          <img
            className="merc-foto"
            src="/images/Mercedes.png"
            alt="Merc"
            width="190px"
            height="60px"
          />
        </div>
      </div>

      <div className="fondo-container4">
        <div className="alp">
          <img
            className="alp-foto"
            src="/images/Alpine.png"
            alt="alp"
            width="150px"
            height="60px"
          />
        </div>
      </div>

      <div className="fondo-container5">
        <div className="red">
          <img
            className="red-foto"
            src="/images/RedBull.png"
            alt="red"
            width="150px"
            height="150px"
          />
        </div>
      </div>

      <div className="fondo-container6">
        <div className="haas">
          <img
            className="haas-foto"
            src="/images/Haas.png"
            alt="haas"
            width="150px"
            height="60px"
          />
        </div>
      </div>

      <div className="fondo-container7">
        <div className="alpha">
          <img
            className="alpha-foto"
            src="/images/AlphaTauri.png"
            alt="alpha"
            width="100px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container8">
        <div className="mcl">
          <img
            className="alp-foto"
            src="/images/McLaren.png"
            alt="mcl"
            width="150px"
            height="60px"
          />
        </div>
      </div>

      <div className="fondo-container9">
        <div className="fer">
          <img
            className="fer-foto"
            src="/images/ferrari.png"
            alt="fer"
            width="170px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container10">
        <div className="wil">
          <img
            className="wil-foto"
            src="/images/Williams.png.png"
            alt="wil"
            width="170px"
            height="20px"
          />
        </div>
      </div>
    </Layout>
  );
};

export default Categorias;
