import React, { useState, useEffect } from "react";
import Layout from "./../components/layout/layout";
import { useCarro } from "../context/carro";
import { useAuth } from "../context/auth";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import toast from "react-hot-toast";
import DropIn from "braintree-web-drop-in-react";
const CarroPagina = () => {
  const [auth, setAuth] = useAuth();
  const [carro, setCarro] = useCarro();
  const [clientToken, setClientToken] = useState("");
  const [instance, setInstance] = useState("");
  const [loading, setLoading] = useState(false);
  const navigate = useNavigate();

  //precio total
  const preciototal = () => {
    try {
      let total = 0;
      carro?.map((item) => {
        total = total + item.precio;
      });
      return total.toLocaleString("es-ES", {
        style: "currency",
        currency: "EUR",
      });
    } catch (error) {
      console.log(error);
    }
  };
  //eliminar producto del carrito
  const eliminarProductoCarro = (pid) => {
    try {
      let miCarro = [...carro];
      let indice = miCarro.findIndex((item) => item._id === pid);
      miCarro.splice(indice, 1);
      setCarro(miCarro);
      localStorage.setItem("carro", JSON.stringify(miCarro));
    } catch (error) {
      console.log(error);
    }
  };

  //obtener token pagos
  const getToken = async () => {
    try {
      const { data } = await axios.get("/api/v1/producto/braintree/token");
      setClientToken(data?.clientToken);
    } catch (error) {
      console.log(error);
    }
  };
  useEffect(() => {
    getToken();
  }, [auth?.token]);

  //handle pagos
  const handlePayment = async () => {
    try {
      setLoading(true);
      const { nonce } = await instance.requestPaymentMethod();
      const { data } = await axios.post("/api/v1/producto/braintree/payment", {
        nonce,
        carro,
      });
      setLoading(false);
      localStorage.removeItem("carro");
      setCarro([]);
      navigate("/panel/user/pedidos");
      toast.success("Pago Completado Correctamente");
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  return (
    <Layout>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "20px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-12">
            <h1 className="text-center p-2 mb-1" style={{ color: "white" }}>
              {`Hola ${auth?.token && auth?.user?.nombre}`}
            </h1>
            <h4 className="text-center" style={{ color: "white" }}>
              {carro?.length
                ? `Tienes ${carro.length} artículos en el carrito ${
                    auth?.token
                      ? ""
                      : "Por favor inicia sesión para realizar la compra"
                  }`
                : "El carro está vacío"}
            </h4>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6">
            {carro?.map((p) => (
              <div className="row mb-2 p-3 card flex-row">
                <div className="col-md-4">
                  <img
                    src={`/api/v1/producto/producto-foto/${p._id}`}
                    className="card-img-top"
                    alt={p.name}
                    width="100px"
                    height={"100px"}
                  />
                </div>
                <div className="col-md-8">
                  <p>{p.nombre}</p>
                  <p>{p.descripcion.substring(0, 30)}</p>
                  <p>Precio: {p.precio}</p>
                  <button
                    className="btn btn-danger"
                    onClick={() => eliminarProductoCarro(p._id)}
                  >
                    Eliminar
                  </button>
                </div>
              </div>
            ))}
          </div>
          <div className="col-md-4 text-center">
            <h4 style={{ color: "white" }}>Productos en el carrito</h4>
            <hr style={{ color: "white" }} />
            <h4 style={{ color: "white" }}>Total : {preciototal()}</h4>
            {auth?.user?.address ? (
              <>
                <div className="mb-3">
                  <h4>Dirección Actual</h4>
                  <h5>{auth?.user?.direccion}</h5>
                  <button
                    className="btn btn-outline-warning"
                    onClick={() => navigate("/panel/user/perfil")}
                  >
                    Actualizar Datos
                  </button>
                </div>
              </>
            ) : (
              <div className="mb-3">
                {auth?.token ? (
                  <button
                    className="btn btn-outline-warning"
                    onClick={() => navigate("/panel/user/perfil")}
                  >
                    Actualizar Datos
                  </button>
                ) : (
                  <button
                    className="btn btn-outline-warning"
                    onClick={() =>
                      navigate("/login", {
                        state: "/carro",
                      })
                    }
                  >
                    Inicia Sesión para realizar la compra
                  </button>
                )}
              </div>
            )}
            <div className="mt-2">
              {!clientToken || !auth?.token || !carro?.length ? (
                ""
              ) : (
                <>
                  <DropIn
                    options={{
                      authorization: clientToken,
                      paypal: {
                        flow: "vault",
                      },
                    }}
                    onInstance={(instance) => setInstance(instance)}
                  />

                  <button
                    className="btn btn-danger"
                    onClick={handlePayment}
                    disabled={loading || !instance || !auth?.user?.direccion}
                  >
                    {loading ? "Procesando ...." : "Realizar Pago"}
                  </button>
                </>
              )}
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CarroPagina;
