import React, { useState, useEffect } from "react";
import Layout from "./../../components/layout/layout";
import MenuAdmin from "./../../components/layout/MenuAdmin";
import toast from "react-hot-toast";
import axios from "axios";
import { Select } from "antd";
import { useNavigate } from "react-router-dom";
const { Option } = Select;

const CrearProducto = () => {
  const navigate = useNavigate();
  const [categorias, setCategorias] = useState([]);
  const [nombre, setNombre] = useState("");
  const [descripcion, setDescripcion] = useState("");
  const [precio, setPrecio] = useState("");
  const [categoria, setCategoria] = useState("");
  const [cantidad, setCantidad] = useState("");
  const [envio, setEnvio] = useState("");
  const [foto, setFoto] = useState("");

  const getTodasCategorias = async () => {
    try {
      const { data } = await axios.get("/api/v1/categoria/get-categoria");
      if (data?.success) {
        setCategorias(data?.categoria);
      }
    } catch (error) {
      console.log(error);
      toast.error("Algo ha ido mal al obtener la categoría");
    }
  };

  useEffect(() => {
    getTodasCategorias();
  }, []);

  const handleCreate = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("nombre", nombre);
      productData.append("descripcion", descripcion);
      productData.append("precio", precio);
      productData.append("cantidad", cantidad);
      productData.append("foto", foto);
      productData.append("categoria", categoria);
      const { data } = axios.post(
        "/api/v1/producto/crear-producto",
        productData
      );
      if (data?.success) {
        toast.error(data?.message);
      } else {
        toast.success("Producto Creado Correctamente");
        navigate("/panel/admin/producto");
      }
    } catch (error) {
      console.log(error);
      toast.error("something went wrong");
    }
  };

  return (
    <Layout title={"Panel - Crear Producto"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <h1 style={{ color: "white" }}>Crear Producto</h1>
            <div className="m-1 w-75">
              <Select
                bordered={false}
                placeholder="Selecciona una categoría"
                size="large"
                showSearch
                className="form-select mb-3"
                onChange={(value) => {
                  setCategoria(value);
                }}
                style={{ fontFamily: "Formula1" }}
              >
                {categorias?.map((c) => (
                  <Option key={c._id} value={c._id}>
                    {c.nombre}
                  </Option>
                ))}
              </Select>
              <div className="mb-3">
                <label className="btn btn-danger col-md-12">
                  {foto ? foto.name : "Subir Foto"}
                  <input
                    type="file"
                    name="foto"
                    accept="image/*"
                    onChange={(e) => setFoto(e.target.files[0])}
                    hidden
                  />
                </label>
              </div>
              <div className="mb-3">
                {foto && (
                  <div className="text-center">
                    <img
                      src={URL.createObjectURL(foto)}
                      alt="product_photo"
                      height={"200px"}
                      className="img img-responsive"
                    />
                  </div>
                )}
              </div>
              <div className="mb-3">
                <input
                  style={{ fontFamily: "Formula1" }}
                  type="text"
                  value={nombre}
                  placeholder="Escribe un nombre"
                  className="form-control"
                  onChange={(e) => setNombre(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <textarea
                  style={{ fontFamily: "Formula1" }}
                  type="text"
                  value={descripcion}
                  placeholder="Descripción del producto"
                  className="form-control"
                  onChange={(e) => setDescripcion(e.target.value)}
                />
              </div>

              <div className="mb-3">
                <input
                  style={{ fontFamily: "Formula1" }}
                  type="number"
                  value={precio}
                  placeholder="Precio del producto"
                  className="form-control"
                  onChange={(e) => setPrecio(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <input
                  style={{ fontFamily: "Formula1" }}
                  type="number"
                  value={cantidad}
                  placeholder="Cantidad disponible"
                  className="form-control"
                  onChange={(e) => setCantidad(e.target.value)}
                />
              </div>
              <div className="mb-3">
                <Select
                  bordered={false}
                  placeholder="Seleccionar Envío"
                  size="large"
                  showSearch
                  className="form-select mb-3"
                  onChange={(value) => {
                    setEnvio(value);
                  }}
                >
                  <Option value="0">No</Option>
                  <Option value="1">Si</Option>
                </Select>
              </div>
              <div className="mb-3">
                <button className="btn btn-danger" onClick={handleCreate}>
                  CREAR PRODUCTO
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default CrearProducto;
