import React, { useState, useEffect } from "react";
import Layout from "./../../components/layout/layout";
import MenuUsuario from "../../components/layout/MenuUsuario";
import axios from "axios";
import { useAuth } from "../../context/auth";
const Pedidos = () => {
  const [orders, setOrders] = useState([]);
  const [auth, setAuth] = useAuth();
  const getPedidos = async () => {
    try {
      const { data } = await axios.get("/api/v1/auth/pedidos");
      setOrders(data);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    if (auth?.token) getPedidos();
  }, [auth?.token]);

  return (
    <Layout title={"Tus pedidos - Footie Fuel"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3" style={{ fontFamily: "Formula1" }}>
            <MenuUsuario />
          </div>
          <div className="col-md-9">
            <h1 style={{ color: "white" }}>Todos los pedidos</h1>
            <p>{JSON.stringify(orders, null, 4)}</p>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Pedidos;
