import React, { useState, useEffect } from "react";
import MenuAdmin from "./../../components/layout/MenuAdmin";
import Layout from "./../../components/layout/layout";
import axios from "axios";
import toast from "react-hot-toast";
import { Link } from "react-router-dom";
import "../../styles/fondo.css";
const Productos = () => {
  const [productos, setProductos] = useState([]);

  //getall products
  const getTodosProductos = async () => {
    try {
      const { data } = await axios.get("/api/v1/producto/get-producto");
      setProductos(data.productos);
    } catch (error) {
      console.log(error);
      toast.error("Algo ha salido mal");
    }
  };

  //lifecycle method
  useEffect(() => {
    getTodosProductos();
  }, []);
  return (
    <Layout>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <h1 className="text-center" style={{ color: "white" }}>
              Listado de Todos los Productos
            </h1>
            <div className="d-flex flex-wrap text-center">
              {productos?.map((p) => (
                <Link
                  key={p._id}
                  to={`/panel/admin/producto/${p.slug}`}
                  className="link-productos"
                >
                  <div
                    className="card m-3 productos"
                    style={{ width: "18rem" }}
                  >
                    <img
                      src={`/api/v1/producto/producto-foto/${p._id}`}
                      className="card-img-top"
                      alt={p.name}
                    />
                    <div className="card-body">
                      <h5 className="card-title">{p.nombre}</h5>
                      <p className="card-text">
                        {" "}
                        {p.descripcion.substring(0, 30)}...
                      </p>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Productos;
