import { useState, useContext, createContext } from "react";

const BuscarContext = createContext();
const BuscarProvider = ({ children }) => {
  const [auth, setAuth] = useState({
    keyword: "",
    resultado: [],
  });

  return (
    <BuscarContext.Provider value={[auth, setAuth]}>
      {children}
    </BuscarContext.Provider>
  );
};

//Lógica para obtener datos
const useBuscar = () => useContext(BuscarContext);

export { useBuscar, BuscarProvider };
