import React from "react";
import { NavLink, Link } from "react-router-dom";
import { useAuth } from "../../context/auth";
import { toast } from "react-hot-toast";
import BuscarEntrada from "../Form/BuscarEntrada";
import { useCarro } from "../../context/carro";
import { Avatar, Badge } from "antd";

const Header = () => {
  const [auth, setAuth] = useAuth();
  const [carro] = useCarro();
  const handleCerrar = () => {
    setAuth({
      ...auth,
      user: null,
      token: "",
    });
    localStorage.removeItem("auth");
    toast.success("Se ha cerrado la sesión correctamente");
  };
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light bg-light">
        <div className="container-fluid">
          <Link to="/" className="navbar-brand" style={{ flex: 1 }}>
            <img src="/images/titulo.png" alt="logo" width="350" height="100" />
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarTogglerDemo01"
            aria-controls="navbarTogglerDemo01"
            aria-expanded="false"
            aria-label="Toggle navigation"
            style={{ flex: 0 }}
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
              <BuscarEntrada />
              <li className="nav-item">
                <NavLink to="/" exact className="nav-link">
                  <li style={{ "--color": "red" }} className="registro">
                    <span className="base" />
                    <span className="title">Inicio</span>
                    <span className="icon">
                      <img
                        src="/images/home.png"
                        alt="registro"
                        width="40"
                        height="40"
                      />
                    </span>
                  </li>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to={"/f1"} className="nav-link">
                  <li style={{ "--color": "red" }} className="f1">
                    <span className="base" />
                    <span className="title">F1</span>
                    <span className="icon">
                      <img
                        src="/images/F1.png"
                        alt="f1"
                        width="70"
                        height="50"
                      />
                    </span>
                  </li>
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/Futbol" className="nav-link">
                  <li style={{ "--color": "red" }} className="futbol">
                    <span className="base" />
                    <span className="title">Futbol</span>
                    <span className="icon">
                      <img
                        src="/images/futbol.png"
                        alt="futbol"
                        width="40"
                        height="40"
                      />
                    </span>
                  </li>
                </NavLink>
              </li>
              {!auth.user ? (
                <>
                  <li className="nav-item">
                    <NavLink to="/Registro" className="nav-link">
                      <li style={{ "--color": "red" }} className="registro">
                        <span className="base" />
                        <span className="title">Registro</span>
                        <span className="icon">
                          <img
                            src="/images/registro.png"
                            alt="registro"
                            width="50"
                            height="50"
                          />
                        </span>
                      </li>
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink to="/login" className="nav-link">
                      <li style={{ "--color": "red" }} className="sesion">
                        <span className="base" />
                        <span className="title">Inicio Sesión</span>
                        <span className="icon">
                          <img
                            src="/images/iniciosesion.png"
                            alt="sesion"
                            width="50"
                            height="50"
                          />
                        </span>
                      </li>
                    </NavLink>
                  </li>
                </>
              ) : (
                <>
                  <li className="nav-item">
                    <NavLink
                      to={`/panel/${auth?.user?.rol === 1 ? "admin" : "user"}`}
                      className="nav-link"
                    >
                      <li style={{ "--color": "red" }} className="sesion">
                        <span className="base" />
                        <span className="title">Panel</span>
                        <span className="icon">
                          <img
                            src="/images/panel.png"
                            alt="sesion"
                            width="30"
                            height="30"
                          />
                        </span>
                      </li>
                    </NavLink>
                  </li>
                  <li className="nav-item">
                    <NavLink
                      onClick={handleCerrar}
                      to="/login"
                      className="nav-link"
                    >
                      <li style={{ "--color": "red" }} className="cerrar">
                        <span className="base" />
                        <span className="title">Cerrar Sesión</span>
                        <span className="icon">
                          <img
                            src="/images/iniciosesion.png"
                            alt="sesion"
                            width="40"
                            height="50"
                          />
                        </span>
                      </li>
                    </NavLink>
                  </li>
                </>
              )}
              <li className="nav-item">
                <Badge count={carro?.length} showZero>
                  <NavLink to="/carro" className="nav-link">
                    <li style={{ "--color": "red" }} className="carrito">
                      <span className="base" />
                      <span className="title">Carrito</span>
                      <span className="icon">
                        <img
                          src="/images/carrito.png"
                          alt="carrito"
                          width="35"
                          height="40"
                        />
                      </span>
                    </li>
                  </NavLink>
                </Badge>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Header;
