import React from "react";

const CategoriaFormulario = ({ handleSubmit, value, setValue }) => {
  return (
    <>
      <form onSubmit={handleSubmit}>
        <div className="mb-3">
          <input
            type="text"
            className="form-control"
            style={{ fontFamily: "Formula1" }}
            placeholder="Introduce una nueva categoría"
            value={value}
            onChange={(e) => setValue(e.target.value)}
          />
        </div>

        <button type="submit" className="btn btn-danger">
          Añadir Categoría
        </button>
      </form>
    </>
  );
};

export default CategoriaFormulario;
