import { useState, useContext, createContext, useEffect } from "react";

const CarroContexto = createContext();
const CarroProvider = ({ children }) => {
  const [carro, setCarro] = useState([]);

  useEffect(() => {
    let productoCarroExistente = localStorage.getItem("carro");
    if (productoCarroExistente) setCarro(JSON.parse(productoCarroExistente));
  }, []);

  return (
    <CarroContexto.Provider value={[carro, setCarro]}>
      {children}
    </CarroContexto.Provider>
  );
};

//Lógica para obtener datos
const useCarro = () => useContext(CarroContexto);

export { useCarro, CarroProvider };
