import React from "react";
import Layout from "./../../components/layout/layout";
import MenuAdmin from "../../components/layout/MenuAdmin";

const Usuarios = () => {
  return (
    <Layout title={"Panel - Todos los Usuarios"}>
      <div
        className="container-fluid row mt-6 home-page background-image2 custom-container"
        style={{
          minHeight: "100vh",
          paddingBottom: "100px",
          paddingTop: "50px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          <div className="col-md-3">
            <MenuAdmin />
          </div>
          <div className="col-md-9">
            <h1 style={{ color: "white" }}>Todos los Usuarios</h1>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default Usuarios;
