import { Routes, Route } from "react-router-dom";
import Home from "./pages/home";
import About from "./pages/About";
import Contacto from "./pages/Contacto";
import Politicas from "./pages/Politicas";
import Noencontrada from "./pages/Noencontrada";
import Registro from "./pages/Auth/Registro";
import Login from "./pages/Auth/Login";
import Panel from "./pages/user/panel";
import RutasPrivadas from "./components/Routes/privadas";
import ForgotPassword from "./pages/Auth/ForgotPassword";
import AdminRutas from "./components/Routes/AdminRutas";
import AdminPanel from "./pages/Admin/AdminPanel";
import CrearCategoria from "./pages/Admin/CrearCategoria";
import CrearProducto from "./pages/Admin/CrearProducto";
import Usuarios from "./pages/Admin/Usuarios";
import Pedidos from "./pages/user/Pedidos";
import Perfil from "./pages/user/Perfil";
import Productos from "./pages/Admin/Productos";
import ActualizarProducto from "./pages/Admin/ActualizarProducto";
import Buscar from "./pages/Buscar";
import ProductoDetalles from "./pages/ProductoDetalles";
import Categorias from "./pages/Categorias";
import FormulaProductos from "./pages/formulaProductos";
import Categoriasfut from "./pages/Categoriasfut";
import FutbolProductos from "./pages/futbolProductos";
import CarroPagina from "./pages/CarroPagina";

function App() {
  return (
    <>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/producto/:slug" element={<ProductoDetalles />} />
        <Route path="/f1" element={<Categorias />} />
        <Route path="/carro" element={<CarroPagina />} />
        <Route path="/f1/:slug" element={<FormulaProductos />} />
        <Route path="/Futbol" element={<Categoriasfut />} />
        <Route path="/Futbol/:slug" element={<FutbolProductos />} />
        <Route path="/buscar" element={<Buscar />} />
        <Route path="/panel" element={<RutasPrivadas />}>
          <Route path="user" element={<Panel />} />
          <Route path="user/perfil" element={<Perfil />} />
          <Route path="user/pedidos" element={<Pedidos />} />
        </Route>
        <Route path="/panel" element={<AdminRutas />}>
          <Route path="admin" element={<AdminPanel />} />
          <Route path="admin/crear-categoria" element={<CrearCategoria />} />
          <Route path="admin/crear-producto" element={<CrearProducto />} />
          <Route path="admin/producto/:slug" element={<ActualizarProducto />} />
          <Route path="admin/productos" element={<Productos />} />
          <Route path="admin/usuarios" element={<Usuarios />} />
        </Route>
        <Route path="/Registro" element={<Registro />} />
        <Route path="/forgot-password" element={<ForgotPassword />} />
        <Route path="/login" element={<Login />} />
        <Route path="/about" element={<About />} />
        <Route path="/contacto" element={<Contacto />} />
        <Route path="/politicas" element={<Politicas />} />
        <Route path="*" element={<Noencontrada />} />
      </Routes>
    </>
  );
}

export default App;
