import { useState, useEffect } from "react";
import axios from "axios";

export default function usarCategoriaFut() {
  // eslint-disable-next-line react-hooks/rules-of-hooks
  const [categoriasfut, setCategoriasFut] = useState([]);

  //obtener todas las categorias
  const getCategoriasFut = async () => {
    try {
      const { data } = await axios.get("/api/v1/categoria/get-categoria");
      setCategoriasFut(data?.categoria.slice(0, 10));
    } catch (error) {
      console.log(error);
    }
  };

  // eslint-disable-next-line react-hooks/rules-of-hooks
  useEffect(() => {
    getCategoriasFut();
  }, []);

  return categoriasfut;
}
