import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Layout from "./../components/layout/layout";
import usarCategoriaFut from "../hooks/usarCategoriaFut.js";
import "../styles/fondo.css";
const Categoriasfut = () => {
  const categorias = usarCategoriaFut();
  return (
    <Layout title={"Todas las categorias"}>
      <div
        className="container-fluid row mt-6 home-page background-image4 custom-container"
        style={{
          paddingBottom: "100px",
          margin: 0,
          paddingRight: 0,
          width: "100%",
        }}
      >
        <div className="row">
          {categorias.map((c) => (
            <div
              className="col-md-6 mt-5 mb-3 gx-3 gy-3 text-center"
              key={c._id}
            >
              <Link to={`/Futbol/${c.slug}`} className="btn btn-danger">
                {c.nombre}
              </Link>
            </div>
          ))}
        </div>
      </div>
      <div className="fondo-container11">
        <div className="liv">
          <img
            className="liv-foto"
            src="/images/liverpool.png"
            alt="liv"
            width="80px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container12">
        <div className="fcb">
          <img
            className="fcb-foto"
            src="/images/barca.png"
            alt="fcb"
            width="90px"
            height="80px"
          />
        </div>
      </div>

      <div className="fondo-container13">
        <div className="mil">
          <img
            className="mil-foto"
            src="/images/milan.png"
            alt="mil"
            width="70px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container14">
        <div className="utd">
          <img
            className="utd-foto"
            src="/images/united.png"
            alt="utd"
            width="110px"
            height="100px"
          />
        </div>
      </div>

      <div className="fondo-container15">
        <div className="atl">
          <img
            className="atl-foto"
            src="/images/atletico.png"
            alt="atl"
            width="70px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container16">
        <div className="city">
          <img
            className="city-foto"
            src="/images/city.png"
            alt="city"
            width="90px"
            height="80px"
          />
        </div>
      </div>

      <div className="fondo-container17">
        <div className="dor">
          <img
            className="dor-foto"
            src="/images/dortmund.png"
            alt="dor"
            width="90px"
            height="90px"
          />
        </div>
      </div>

      <div className="fondo-container18">
        <div className="bay">
          <img
            className="bay-foto"
            src="/images/bayern.png"
            alt="bay"
            width="90px"
            height="80px"
          />
        </div>
      </div>

      <div className="fondo-container19">
        <div className="psg">
          <img
            className="psg-foto"
            src="/images/psg.png"
            alt="psg"
            width="110px"
            height="100px"
          />
        </div>
      </div>

      <div className="fondo-container20">
        <div className="mad">
          <img
            className="mad-foto"
            src="/images/584a9b47b080d7616d298778.png"
            alt="mad"
            width="80px"
            height="100px"
          />
        </div>
      </div>
    </Layout>
  );
};

export default Categoriasfut;
